import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getProjects } from '../../actions/projectActions';
import Spinner from '../common/Spinner';
import ProjectFeed from './ProjectFeed';
import classnames from 'classnames';
import isEmpty from '../../utils/isEmpty';
class Projects extends Component {
    constructor() {
        super();
        this.state = {
            totalPages: 0,
            currentPage: 1,
            range: []
        };
        this.fetchPageList = this.fetchPageList.bind(this);
    }
    componentDidMount() {
        this.props.getProjects(5, 1);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.project.projects) {
            const projects = nextProps.project.projects;
            const pages = [];
            projects.totalPages = !isEmpty(projects.pages) ? projects.pages : 0;
            projects.currentPage = !isEmpty(projects.page) ? projects.page : 1;
            for (var i = 1; i <= projects.pages; i++) {
                pages.push(i);
            }
            this.setState({
                totalPages: projects.totalPages,
                currentPage: projects.currentPage,
                range: pages
            })
        }
    }
    fetchPageList(pageNumber) {
        if (pageNumber === undefined) {
            pageNumber = '1';
        }
        this.props.getProjects(5, pageNumber)
    }
    render() {
        const { projects, loading } = this.props.project;
        const { currentPage, range, totalPages } = this.state;
        let Content;
        let Paginate;
        if (projects.docs === undefined || loading) {
            Content = <Spinner />
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="25%">Tn</th>
                            <th width="25%" className="hidden-sm hidden-xs">Ngy</th>
                            <th width="25%">nh</th>
                            <th width="25%"></th>
                        </tr>
                    </thead>

                    <tbody>
                        <ProjectFeed projects={projects.docs} />
                    </tbody>
                </table>
            )
        }
        //Paginate
        Paginate = (
            <ul className="pagination pull-right page-list">
                {currentPage !== 1 ? (<li><a href="#0" onClick={this.fetchPageList.bind(this, 1)}></a></li>) : null}
                {currentPage !== 1 ? (<li><a href="#0" onClick={this.fetchPageList.bind(this, currentPage - 1)}> Prev</a></li>) : null}
                {range.map((i, index) =>
                    (<li key={index} className={currentPage === i ? classnames('active') : null}>
                        <a href="#0" onClick={this.fetchPageList.bind(this, i)}>{i}</a>
                    </li >)
                )}
                {currentPage !== totalPages ? (<li><a href="#0" onClick={this.fetchPageList.bind(this, currentPage + 1)}>Next </a></li >) : null}
                {currentPage !== totalPages ? (<li><a href="#0" onClick={this.fetchPageList.bind(this, totalPages)}></a></li >) : null}
            </ul >
        )
        return (
            <div>
                <Breadcrumb name="Ghi ch" />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Danh mc project</h4>
                            <div className="widget-toolbar no-border">
                                <Link to="/create-projects" className="btn btn-sm btn-info">
                                    <i className="ace-icon fa fa-plus"></i> Thm mi danh mc project
                                </Link>
                            </div>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                <div className="search-active-form">
                                    <div className="pageSizebox pull-right">
                                        <div className="pageSizeNumber">
                                            Tng s  n: {projects.total}
                                        </div>
                                    </div>
                                </div>
                                {Content}
                            </div>
                            <div className="widget-toolbox padding-8 clearfix">
                                {Paginate}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Projects.propTypes = {
    getProjects: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({
    project: state.project
});
export default connect(mapStateToProps, { getProjects })(Projects);
