import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { deleteNote } from '../../actions/noteActions';

class NoteItem extends Component {
    onDeleteClick(id) {
        if (window.confirm("Bn c mun xa khng ?")) {
            this.props.deleteNote(id);
        }
    }
    render() {
        const { note } = this.props;
        return (
            <tr>
                <td dangerouslySetInnerHTML={{__html: note.content}}></td>
                <td>
                    <div className="btn-group">
                        <button className="btn btn-xs btn-info">
                            <Link to={`/edit-notes/${note._id}`} style={{ color: '#FFF' }} >
                                <i className="ace-icon fa fa-pencil bigger-120"></i>
                            </Link>
                        </button>
                        <button className="btn btn-xs btn-danger">
                            <i className="ace-icon fa fa-trash-o bigger-120" onClick={this.onDeleteClick.bind(this, note._id)}></i>
                        </button>
                    </div>
                </td>
            </tr >
        )
    }
}
NoteItem.defaultProps = {
    showActions: true
}
NoteItem.propTypes = {
    note: PropTypes.object.isRequired,
    deleteNote: PropTypes.func.isRequired
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { deleteNote })(NoteItem);

