//Login
import Login from './components/login/Login';
//Home
import Home from './components/home/Home';
//Timeline
import Timelines from './components/timelines/Timelines';
import CreateTimeline from './components/timelines/CreateTimeline';
import EditTimeline from './components/timelines/EditTimeline';
//Not found
import NotFound from './components/not-found/NotFound';
//Note
import Notes from './components/note/Notes';
import CreateNote from './components/note/CreateNote';
import EditNote from './components/note/EditNote';
//Project
import Project from './components/project/Projects';
import CreateProject from './components/project/CreateProject';
import EditProject from './components/project/EditProject';
//Layout
import PrivateLayout from './components/common/PrivateLayout';
import PublicLayout from './components/common/PublicLayout';

const routes = [
    {
        path: "/",
        exact: true,
        layout: PrivateLayout,
        main: Home
    },
    {
        path: "/timelines",
        exact: true,
        layout: PrivateLayout,
        main: Timelines
    },
    {
        path: "/create-timelines",
        exact: true,
        layout: PrivateLayout,
        main: CreateTimeline
    },
    {
        path: "/edit-timelines/:id",
        exact: true,
        layout: PrivateLayout,
        main: EditTimeline
    },
    {
        path: "/notes",
        exact: true,
        layout: PrivateLayout,
        main: Notes
    },
    {
        path: "/create-notes",
        exact: true,
        layout: PrivateLayout,
        main: CreateNote
    },
    {
        path: "/edit-notes/:id",
        exact: true,
        layout: PrivateLayout,
        main: EditNote
    },
    {
        path: "/projects",
        exact: true,
        layout: PrivateLayout,
        main: Project
    },
    {
        path: "/create-projects",
        exact: true,
        layout: PrivateLayout,
        main: CreateProject
    },
    {
        path: "/edit-projects/:id",
        exact: true,
        layout: PrivateLayout,
        main: EditProject
    },
    {
        path: "/login",
        exact: true,
        layout: PublicLayout,
        main: Login
    },
    {
        path: "/not-found",
        exact: true,
        layout: PublicLayout,
        main: NotFound
    }
];

export default routes;