import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Breadcrumb from '../common/Breadcrumb';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getNotes } from '../../actions/noteActions';
import Spinner from '../common/Spinner';
import NoteFeed from './NoteFeed';
class ShowNote extends Component {
    componentDidMount() {
        this.props.getNotes();
    }
    render() {
        const { notes, loading } = this.props.note;
        let Content;
        if (notes === null || loading) {
            Content = <Spinner />
        } else {
            Content = (
                <table id="simple-table" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th width="90%">Ni dung</th>
                            <th width="10%">Link</th>
                        </tr>
                    </thead>

                    <tbody>
                        <NoteFeed notes={notes} />
                    </tbody>
                </table>
            )
        }
        return (
            <div>
                <Breadcrumb name='Ghi ch' />
                <div className="page-content">
                    <div className="widget-box">
                        <div className="widget-header">
                            <h4 className="widget-title">Ghi ch</h4>
                            <div className="widget-toolbar no-border">
                                <Link to={`/create-notes`} className="btn btn-sm btn-info">
                                    <i className="ace-icon fa fa-plus"></i> Thm mi note
                                </Link>
                            </div>
                        </div>
                        <div className="widget-body">
                            <div className="widget-main">
                                {Content}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
ShowNote.propTypes = {
    getNotes: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({
    note: state.note
});
export default connect(mapStateToProps, { getNotes })(ShowNote);